import React, {Component} from 'react'
import {connect} from 'react-redux'
import moment from 'moment'
import axios from 'axios'
import { Table, Modal, Space, Button, Tooltip,Select, Popconfirm, message, Form, Input, Upload } from 'antd';
import {actionCreators} from './store'
import {actionCreators as trainModelActionCreators } from '../trainModel/store'
import {ContentWapper,ContentBox,AddBtnWapper} from './style'
import {
    InboxOutlined,
    DeleteOutlined
} from '@ant-design/icons';
import DownloadBtn from '../../../components/downloadBtn'

const { Column } = Table;
const { Dragger } = Upload;
const { Option } = Select;

const layout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset:6, span: 16 },
};

class TrainSet extends Component{
    
    
    state = { 
        visible: false ,
        type:'add',
        data:{}
    };

    showModal = (type,initData) => {
        this.setState({
            visible: true,
            type,
            data:initData
        });
    };

    handleFinish = values => {
        console.log(values);

        const formData = new FormData();
        formData.append('file', values.file.file);
        formData.append('user_filename', values.user_filename);
        formData.append('msg_type','upload')
        formData.append('env_name',values.env_name)
        formData.append('desc',values.desc)
        let instance = axios.create({
            baseURL:'http://10.186.0.210:8001/',
            timeout:1000,
            headers:{"Content-Type":"multipart/form-data"}
        })
        let url='api/v1/auto-lm/trainset/'
        console.log(url)

        instance.post(url,formData).then(res=>{
            console.log(res)
            this.reload()
        })
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
        visible: false,
        });
    };

    renderModal(){
        let modelType = "添加"
        if(this.state.type==="edit"){
            modelType = "编辑"
        }

        console.log(this.state.type,this.state.data)

        return(
            <Modal
                title={modelType + "训练集"}
                visible={this.state.visible}
                onCancel={this.handleCancel}
                footer={null}
                destroyOnClose
            >
                <Form
                    {...layout}
                    name="basic"
                    initialValues={this.state.data}
                    onFinish={this.handleFinish}
                // onFinishFailed={onFinishFailed}
                >
                    <Form.Item
                        label="训练集名称"
                        name="user_filename"
                        rules={[{ required: true, message: '请输入训练集名称!' }]}
                    >
                        <Input/>
                    </Form.Item>
                    <Form.Item
                        label="所属训练环境"
                        name="env_name"
                        rules={[{ required: true, message: '请选择训练环境!' }]}
                    >
                        <Select 
                            disabled={this.state.type==="edit"}
                        >
                            {this.props.envs.map(item=>(
                                <Option value={item.name} key={item.name}>{item.display_name}</Option>
                            ))}
                            
                        </Select>
                    </Form.Item>
                    <Form.Item
                        label="训练集描述"
                        name="desc"
                    >
                        <Input/>
                    </Form.Item>

                    <Form.Item
                        label="上传训练集"
                        name="file"
                        rules={[{ required: true, message: '请选择训练集!' }]}
                    >
                        <Dragger 
                            name='file'
                            accept=".txt"
                            beforeUpload={(file)  => {
                                const isLt10M = file.size / 1024 / 1024 <= 10;
                                if (!isLt10M) {
                                    message.error("文件大小限制在10M以下！");
                                    return false;
                                }
                                return false;
                            }}
                        >
                            <p className="ant-upload-drag-icon">
                                <InboxOutlined />
                            </p>
                            <p className="ant-upload-text">点击或拖拽到这个区域进行上传</p>
                            <p className="ant-upload-hint">
                                支持txt格式，且文件大小不能超过10MB
                            </p>
                        </Dragger>
                    </Form.Item>

                    <Form.Item {...tailLayout}>
                        <Button type="primary" htmlType="submit">
                            确定
                        </Button>
                    </Form.Item>
                </Form>
            </Modal>
        )
    }

    render(){
          
        function cancel(e) {
            console.log(e);
        }
        const { tableData, handleDeleteTableItem } = this.props;

        return(
            <ContentWapper>
                <ContentBox>
                    <AddBtnWapper>
                        <Button type="primary" onClick={()=>this.showModal("add",{
                            env_name:this.props.envs[0].name,
                            desc:'',
                            user_filename:'',
                            file:{}
                        })}>
                        添加训练集
                    </Button>
                    </AddBtnWapper>
                    {this.renderModal()}
                    <Table dataSource={tableData} rowKey={(text, record) => text.file_id}>
                        <Column title="环境名称" width="10" align="center" dataIndex="env_name" key="envName" />
                        <Column title="训练集名称" width="10" align="center" dataIndex="user_filename" key="trainSetName" />
                        <Column title="训练集描述" align="center" dataIndex="desc" key="trainSetDesc" />
                        <Column title="创建时间"  align="center"dataIndex="created_at" key="createTime"  render={(text)=>(
                            <span>{moment(text).format("YYYY-MM-DD HH:mm:ss")}</span>
                        )}/>
                        <Column
                            title="操作"
                            key="action"
                            align="center"
                            render={(text, record, index) => (
                                <Space size="small">
                                    <DownloadBtn
                                        api_url={"api/v1/auto-lm/download/?type=train&id="+record.file_id}
                                        btnType="iconBtn"
                                        fileName={record.user_filename}
                                    />
                                    <Popconfirm title="确认删除？" onConfirm={()=>handleDeleteTableItem(record,index)} onCancel={cancel} okText="确定" cancelText="取消">
                                        <Tooltip placement="bottomLeft" title='删除'>
                                            <Button type="danger" size="small" icon={<DeleteOutlined />}></Button>
                                        </Tooltip>
                                    </Popconfirm>
                                </Space>
                            )}
                        />
                    </Table>
                </ContentBox>
            </ContentWapper>
        )
    }
    componentDidMount(){
        console.log('xxx')
        this.props.handleInitTableData()
        if(this.props.envs.size===0){
            this.props.handleInitEnvData()
        }
    }

    reload = () => {
        this.componentDidMount()
    }
}

const mapStateToProps = (state)=>{
    return {
        tableData: state.getIn(['trainSet','tableData']),
        envs:state.getIn(['trainModel','envs'])
    }
}

const mapDispatch = (dispatch) =>({
    handleInitEnvData(){
        dispatch(trainModelActionCreators.handleChangeEnv())
    },
    handleInitTableData(){
        dispatch(actionCreators.handleInitTableData())
    },
    handleDeleteTableItem(data,index){
        dispatch(actionCreators.handleDeleteTableItem(index,data))
        // message.success('删除成功');
    }
})

export default connect(mapStateToProps,mapDispatch)(TrainSet);